import React, {Component} from 'react';
import {Modal, Button, Row, Col, Form,Dropdown} from 'react-bootstrap';
import { Link } from 'react-router-dom';

import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';


export class SignUp extends Component{

    constructor(props){
        super(props);
      
        this.state = {Reg:[],snackbaropen: false, snackbarmsg: ''};
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    snackbarClose = (event) =>{
      this.setState({snackbaropen:false});
    };


    handleSubmit(event){
        event.preventDefault();

        fetch('https://localhost:44311/api/Register',{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                id:null,
                name:event.target.name.value,
                email:event.target.email.value,
                mobile:event.target.mobile.value,
                gender:event.target.gender.value,
                username:event.target.username.value,
                password:event.target.password.value
            })
        })
        .then(res=> res.json())
        .then((result)=>
        {
            //alert(result);
            this.setState({snackbaropen:true, snackbarmsg:result});
        },
        (error)=>{
          //alert('Failed')
          this.setState({snackbaropen:true, snackbarmsg:'failed'});
        }
        )
    }

    render(){
        return(
            <div className="container bg-light p-3">

      <div className="mt-5 d-flex offset-md-4">
            <h3>New Registration</h3>
            </div>

            <Snackbar 
            anchorOrigin={{vertical:'bottom',horizontal:'center'}}
            open = {this.state.snackbaropen}
            autoHideDuration = {3000}
            onClose={this.snackbarClose}
            
            message = {<span id="message-id">{this.state.snackbarmsg}</span>}
            action={[
            <IconButton
            key="close"
            arial-label="Close"
            color="inherit"
            onClick={this.snackbarClose}
            >
              x
            </IconButton>
            ]}
            />
            
            <Row className="offset-md-3 mt-3">
                          <Col sm={6}>
                          <Form onSubmit={this.handleSubmit}>
            
                          <Form.Group controlId="name">
                          <Form.Label>Name</Form.Label>
                          <Form.Control
                            type="text"
                            name="name"
                            required
                            placeholder="Enter Your Name"
                           />
                          </Form.Group>
            
                        
            
            
                          <Form.Group controlId="email">
                          <Form.Label>E-mail</Form.Label>
                          <Form.Control
                            type="text"
                            name="email"
                            required
                            placeholder="Enter E-mail Address"
                           />
                          </Form.Group>
            
                          <Form.Group controlId="mobile">
                          <Form.Label>Mobile</Form.Label>
                          <Form.Control
                            type="text"
                            name="mobile"
                            required
                            placeholder="Enter Mobile Number"
                           />
                          </Form.Group>
            
                          <Form.Group controlId="gender">
                          <Form.Label>Gender</Form.Label>
                          <Form.Control
                            type="text"
                            name="gender"
                            required
                            placeholder="Enter Gender"
                           />
          </Form.Group>  

                         {/*} <Form.Group controlId="gender">
                          <Form.Label>Gender</Form.Label>
                         
                         <Form.Control as="select">
                            {this.state.Reg.map(User =>
                            <option key={User.id}>{User.name}</option>
                                )}
                            </Form.Control>
                            </Form.Group>*/}

                            {/*----*/}

                            

                          <Form.Group controlId="username">
                          <Form.Label>Username</Form.Label>
                          <Form.Control
                            type="text"
                            name="username"
                            required
                            placeholder="Enter Username"
                           />
                          </Form.Group>
            
                          <Form.Group controlId="password">
                          <Form.Label>Password</Form.Label>
                          <Form.Control
                            type="password"
                            name="password"
                            required
                            placeholder="Enter Password"
                           />
                          </Form.Group>
            
                            
                                <Form.Group>
                              <Button variant="primary" type="submit" className="form-control">
                              SignUp
                              </Button>
                          </Form.Group>
                          

                          </Form>

                            <center>
                          <Link to="/LOGIN">Already Registered? Click Here</Link>
                           </center>

                          </Col>
                      </Row>


                      <br/>
                      <br/><br/><br/><br/><br/>
            
                      </div>

                      
            
        );
    }
}

export default SignUp;