import React, {Component} from 'react';
import {Modal, Button, Row, Col, Form} from 'react-bootstrap';
import { Link,Redirect, useHistory } from 'react-router-dom';
import '../CSS/LOGIN.css'

import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';

function loginfunction(){
    const history = new useHistory();
}

export class LOGIN extends Component{   

    constructor(props){
        super(props);
      
        this.state = {Reg:[],snackbaropen: false, snackbarmsg: ''};
        this.handleSubmit = this.handleSubmit.bind(this);
    }        

    snackbarClose = (event) =>{
      this.setState({snackbaropen:false});
    };

    

    handleSubmit(event){
        event.preventDefault();
        //const history = useHistory();

        fetch('https://localhost:44311/api/Login',{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                id:null,
                username:event.target.username.value,
                password:event.target.password.value
            })
        })
        .then(res=> res.json())
        .then((result)=>
        {
            
            //alert(result);
            //this.setState({snackbaropen:true, snackbarmsg:result});
            this.props.history.push({pathname:"/Register",
            state:{
            }
        })
        },
        (error)=>{
          //alert('Failed')
          this.setState({snackbaropen:true, snackbarmsg:'failed'});
        }
        )
    }
    
    render(){
        return(
            <div className="container bg-light mt-2 p-4">

      <div className="mt-5 d-flex offset-md-4 ">
            <h3 className="mt-2">LOGIN FORM</h3>
            </div>

            <Snackbar 
            anchorOrigin={{vertical:'bottom',horizontal:'center'}}
            open = {this.state.snackbaropen}
            autoHideDuration = {3000}
            onClose={this.snackbarClose}
            
            message = {<span id="message-id">{this.state.snackbarmsg}</span>}
            action={[
            <IconButton
            key="close"
            arial-label="Close"
            color="inherit"
            onClick={this.snackbarClose}
            >
              x
            </IconButton>
            ]}
            />
            
            <Row className="offset-md-3 mt-2">
                          <Col sm={6}>
                          <Form onSubmit={this.handleSubmit}>
            
                          
                          <Form.Group controlId="username">
                          <Form.Label>Username</Form.Label>
                          <Form.Control
                            type="text"
                            name="username"
                            required
                            placeholder="Enter Username"
                           />
                          </Form.Group>
            
                          <Form.Group controlId="password">
                          <Form.Label>Password</Form.Label>
                          <Form.Control
                            type="password"
                            name="password"
                            required
                            placeholder="Enter Password"
                           />
                          </Form.Group>
            
            
                          <Form.Group>
                              <Button variant="primary" type="submit">
                              LOGIN
                              </Button>
                          </Form.Group>
                          </Form>

                          <Link to="/SignUp">Not Registerd Yet? Click Here</Link>
                          
                          </Col>
                      </Row>
            
                      </div>
            
        );
    }
}

export default LOGIN;