import React, {Component} from 'react';
import {Modal, Button, Row, Col, Form} from 'react-bootstrap';

import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';


export class AddRegisterModal extends Component{

    constructor(props){
        super(props);
      
        this.state = {Reg:[],snackbaropen: false, snackbarmsg: ''};
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount(){
      fetch('https://localhost:44311/api/Registers')
      .then(response => response.json())
      .then(data => {
      this.setState({Reg:data});
      });
  }

    snackbarClose = (event) =>{
      this.setState({snackbaropen:false});
    };


    handleSubmit(event){
        event.preventDefault();

        fetch('https://localhost:44311/api/Register',{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                id:null,
                name:event.target.name.value,
                email:event.target.email.value,
                mobile:event.target.mobile.value,
                gender:event.target.gender.value,
                username:event.target.username.value,
                password:event.target.password.value
            })
        })
        .then(res=> res.json())
        .then((result)=>
        {
            //alert(result);
            this.setState({snackbaropen:true, snackbarmsg:result});
        },
        (error)=>{
          //alert('Failed')
          this.setState({snackbaropen:true, snackbarmsg:'failed'});
        }
        )
    }

    render(){
        return(
          <div className="container">

<Snackbar 
anchorOrigin={{vertical:'bottom',horizontal:'center'}}
open = {this.state.snackbaropen}
autoHideDuration = {3000}
onClose={this.snackbarClose}

message = {<span id="message-id">{this.state.snackbarmsg}</span>}
action={[
<IconButton
key="close"
arial-label="Close"
color="inherit"
onClick={this.snackbarClose}
>
  x
</IconButton>
]}
/>

            <Modal
        {...this.props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Add User
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>      
            
        <Row>
              <Col sm={6}>
              <Form onSubmit={this.handleSubmit}>

              <Form.Group controlId="name">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                name="name"
                required
                placeholder="Enter Your Name"
               />
              </Form.Group>

            


              <Form.Group controlId="email">
              <Form.Label>E-mail</Form.Label>
              <Form.Control
                type="text"
                name="email"
                required
                placeholder="Enter E-mail Address"
               />
              </Form.Group>

              <Form.Group controlId="mobile">
              <Form.Label>Mobile</Form.Label>
              <Form.Control
                type="text"
                name="mobile"
                required
                placeholder="Enter Mobile Number"
               />
              </Form.Group>

             <Form.Group controlId="gender">
              <Form.Label>Gender</Form.Label>
              <Form.Control
                type="text"
                name="gender"
                required
                placeholder="Enter Gender"
               />
              </Form.Group>

              {/*<Form.Group controlId="gender">
              <Form.Label>Gender</Form.Label>
             
             <Form.Control as="select">
                {this.state.Reg.map(User =>
                <option key={User.id}>{User.name}</option>
                    )}
                </Form.Control>
                </Form.Group>*/}

              <Form.Group controlId="username">
              <Form.Label>Username</Form.Label>
              <Form.Control
                type="text"
                name="username"
                required
                placeholder="Enter Username"
               />
              </Form.Group>

              <Form.Group controlId="password">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                name="password"
                required
                placeholder="Enter Password"
               />
              </Form.Group>


              <Form.Group>
                  <Button variant="primary" type="submit">
                  Add User
                  </Button>
              </Form.Group>
              </Form>
              </Col>
          </Row>


        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={this.props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>

      </div>
        );
    }


}
