import React,{Component} from 'react';
import {Table} from 'react-bootstrap';
import {Button, ButtonToolbar} from 'react-bootstrap';

import {AddRegisterModal} from './AddRegisterModal';
import {EditRegisterModal} from './EditRegisterModal';

export class Register extends Component {

    constructor(props){
        super(props);
        this.state ={Reg:[],addModalShow : false,editModalShow : false}
    }

    componentDidMount(){
        this.refreshList();
    }

    refreshList(){
        fetch('https://localhost:44311/api/Register')
        .then(response=> response.json())
        .then(data =>{
            this.setState({Reg:data});
        }            
            );     
     }

     componentDidUpdate(){
        this.refreshList();
    }

    deleteUser(Userid)
    {
        if(window.confirm('Are you sure?'))
        {
            fetch('https://localhost:44311/api/Register?id='+Userid,{
                method:'DELETE',
                header:{'Accept':'application/json',
                'Content-Type':'application/json'
            }
            })
        }
    }

    render(){

        const {Reg, id, name,email, mobile,gender, username, password} = this.state;
        let addModalClose =() => this.setState({addModalShow:false});
        let editModalClose =() => this.setState({editModalShow:false});

        return(
            <div className="container">
            <Table className="mt-4 table" striped bordered hover size="sm">
            <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Mobile</th>
                    <th>Gender</th>
                    <th>Username</th>
                    <th>Option</th>
                </tr>
            </thead>
            <tbody>
                {Reg.map(User=>
                   <tr key ={User.id}> 
                   <td>{User.id}</td>
                   <td>{User.name}</td>
                   <td>{User.email}</td>
                   <td>{User.mobile}</td>
                   <td>{User.gender}</td>
                   <td>{User.username}</td>    
                   <td>
                   <ButtonToolbar>
<Button
className="mr-2" variant="info"
onClick= {()=> this.setState({editModalShow:true, 
    id:User.id, name:User.name, 
    email:User.email, mobile:User.mobile,
    gender:User.gender, username:User.username, password:User.password})}
>
    Edit
</Button>

<Button className="mr-2" 
onClick={()=> this.deleteUser(User.id)} 
variant="danger"
>Delete</Button>

<EditRegisterModal
show = {this.state.editModalShow}
onHide={editModalClose}
id = {id}
name = {name}
email={email}
mobile={mobile}
gender={gender}
username={username}
password={password}
/>

</ButtonToolbar>
                       </td>               
                   </tr>
                   )}
            </tbody>
            </Table>
            <ButtonToolbar>
            <Button
    variant='primary'
    onClick={()=> this.setState({addModalShow: true})} 
    >Add User</Button>

    <AddRegisterModal
    show={this.state.addModalShow}
    onHide={addModalClose}
    />
            </ButtonToolbar>
</div>
        )
    }

}

export default Register;